'use strict';

var Chord = require('./chord');

var voicePattern = /^([a-zA-Z-_]+)([0-9]*):\s(.*)/;

function tokenize(instrument, data) {
  var re = /[^\s]+/g;
  var events = [];
  var match;

  while (match = re.exec(data)) {
    events.push({
      index: match.index,
      content: instrument == 'c' ? Chord.parse(match[0]) : match[0]
    });
  }

  return events;
}

function parseVoice(voice) {
  if (!voice) {
    return;
  }

  var match = voice.match(voicePattern);

  if (!match) {
    throw new Error("Voice doesn't match ".concat(voicePattern, ": ").concat(voice));
  }

  var instrument = match[1];
  var data = match[3];
  return tokenize(instrument, data);
}

function parsePhrase(phrase) {
  return phrase.split(/\n/).reduce(function (phrase, voice) {
    if (voice) {
      var match = voice.match(voicePattern);
      var voiceName = "".concat(match[1]).concat(match[2] || '1');
      phrase.set(voiceName, parseVoice(voice));
    }

    return phrase;
  }, new Map());
}

function parseVerse(verse) {
  return verse.split(/[\n]{2,}/).map(function (phrase) {
    return parsePhrase(phrase);
  });
}

module.exports = {
  parseVerse: parseVerse
};